<template>
  <div class="proauth-page" :class="{loading}">
    <div class="proauth-form">
      <vxe-input class="form-input" v-model="formData.no" placeholder="请输入编号或名称查询" @keydown="keydownEvent"></vxe-input>
      <vxe-button class="form-button" type="button" @click="searchEvent">查询</vxe-button>
    </div>
    <div class="proauth-pager">
      <vxe-pager
        class="form-page"
        :current-page.sync="pageVO.currentPage"
        :page-size.sync="pageVO.pageSize"
        :total="pageVO.total"
        :layouts="['PrevPage', 'NextPage']"
        @page-change="pageChangeEvent">
      </vxe-pager>
    </div>
    <div class="proauth-list">
      <div class="proauth-item" v-for="(item, index) in list" :key="index" :title="`${item.no} - ${$utils.getAuthUserType(item.aType)}授权`">
        <div class="proauth-content">
          <h4 class="item-title">
            <i class="fa" :class="$utils.getAuthUserIcon(item.aType)" style="margin-right: 0.5em;"></i>
            <span>{{ item.aName }}</span>
          </h4>
          <div class="proauth-info">
            <span class="item-no">编号：{{ item.no }}</span>
            <span class="item-date">授权时间：{{ item.date }}</span>
          </div>
        </div>
      </div>
      <div v-if="!loading && !list.length" class="no-data">
        <span>可能编号不正确，找不到对应的记录！</span>
      </div>
    </div>
    <div class="loading-wrapper">
      <div class="loading--spinner"></div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import XEAjax from 'xe-ajax'

export default {
  data () {
    return {
      loading: false,
      pageVO: {
        currentPage: 1,
        pageSize: 6,
        total: 1
      },
      formData: {
        no: ''
      },
      list: []
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo'
    ])
  },
  created () {
    this.searchEvent(true)
  },
  methods: {
    keydownEvent ({ $event }) {
      if ($event.key === 'Enter') {
        this.searchEvent()
      }
    },
    searchEvent (isInit) {
      const no = this.formData.no ? this.formData.no.trim() : ''
      this.loading = !isInit
      setTimeout(() => {
        XEAjax.get(`/vxe/api/pub/vxetable/auth/page/list/${this.pageVO.pageSize}/${this.pageVO.currentPage}?no=${no}`).then(data => {
          this.loading = false
          const rest = this.$utils.getRest(data)
          this.list = rest.result
          this.pageVO.total = rest.page.total
        }).catch(e => {
          this.loading = false
        })
      }, this.pageVO.currentPage === 1 ? 0 : 10000)
    },
    pageChangeEvent () {
      this.searchEvent()
    }
  }
}
</script>

<style lang="scss" scoped>
.proauth-page {
  .loading-wrapper {
    display: none;
  }
  &.loading {
    .loading-wrapper {
      display: block;
    }
  }
}
.proauth-form {
  padding: 5px 0 10px 0;
  text-align: center;
  .form-input {
    width: 400px;
    vertical-align: middle;
    & /deep/ .vxe-input--inner {
      border-radius: 4px 0 0 4px;
    }
  }
  .form-button.type--button {
    vertical-align: middle;
    margin-left: 0;
    border-radius: 0 4px 4px 0;
  }
}
.proauth-pager {
  width: 1200px;
  margin: 0 auto;
  text-align: right;
  .form-page {
    background: transparent;
  }
}
.proauth-list {
  width: 1200px;
  margin: 0 auto;
  &:after {
    content: "";
    display: block;
    clear: both;
    height: 0;
    overflow: hidden;
    visibility: hidden;
  }
  .proauth-item {
    width: 600px;
    float: left;
    width: 50%;
    padding: 10px;
  }
  .proauth-content {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 4px;
    border: 1px solid #EBEEF5;
    box-shadow: 1px 2px 10px #eee;
    background-color: #fff;
  }
  .item-title {
    margin: 0;
    padding: 20px 15px;
    border-bottom: 1px solid #EBEEF5;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .proauth-info {
    padding: 20px 15px;
  }
  .item-date {
    padding-left: 20px;
  }
  .no-data {
    text-align: center;
    line-height: 2em;
  }
}
.loading-wrapper {
  display: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 99;
  user-select: none;
  background-color: rgba(0, 0, 0, 0.2);
  .loading--spinner {
    width: 56px;
    height: 56px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    &:before,
    &:after {
      content: "";
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: #409eff;
      opacity: 0.6;
      position: absolute;
      top: 0;
      left: 0;
      animation: bounce 2.0s infinite ease-in-out;
    }
    &:after {
      animation-delay: -1.0s;
    }
  }
  @keyframes bounce {
    0%, 100% {
      transform: scale(0);
    } 50% {
      transform: scale(1);
    }
  }
}
</style>
