<template>
  <div class="filter-page">
    <div class="filter-preview">
      <div class="preview-item" v-for="gItem in filterRenderList" :key="gItem.gCode">
        <div class="preview-content">
          <comp-title :selectGroupComp="gItem" :selectComp="getSelectComp(gItem)"></comp-title>
          <img class="preview-img" :src="imgMaps[gItem.selected]">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import CompTitle from './CompTitle'

export default {
  components: {
    CompTitle
  },
  data () {
    window.aa = this
    return {
      imgMaps: {
        FilterComplexInput: require('../assets/image/plugin/FilterComplexInput.gif'),
        FilterComplexInput_Enterprise: require('../assets/image/plugin/FilterComplexInput.gif'),
        FilterComplexInput_Personal: require('../assets/image/plugin/FilterComplexInput.gif'),
        FilterCombination: require('../assets/image/plugin/FilterCombination.gif'),
        FilterCombination_Enterprise: require('../assets/image/plugin/FilterCombination.gif'),
        FilterCombination_Personal: require('../assets/image/plugin/FilterCombination.gif')
      }
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList',
      'appCompList'
    ]),
    filterRenderList () {
      return this.appCompList.filter(item => item.gType === 'filters' && item.options.length > 0)
    }
  },
  destroyed () {
    clearTimeout(this.downTime)
  },
  methods: {
    getSelectComp (gItem) {
      if (gItem) {
        return gItem.options.find(item => item.code === gItem.selected)
      }
      return null
    },
    getCompPrivilege (gItem) {
      return this.loginToken && this.loginInfo && this.loginInfo.compList && this.loginInfo.compList.find(comp => gItem.options.some(item => item.code === comp.appCode))
    }
  }
}
</script>

<style lang="scss" scoped>
.help-icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  font-size: 12px;
  border-radius: 50%;
  margin: 0 5px;
  color: #fff;
  background-color: #000;
  text-align: center;
  cursor: help;
}
.filter-preview {
  width: 1200px;
  margin: 0 auto;
  &:after {
    content: "";
    display: block;
    clear: both;
    height: 0;
    overflow: hidden;
    visibility: hidden;
  }
  .preview-item {
    width: 600px;
    height: 420px;
    float: left;
    width: 50%;
    padding: 10px;
  }
  .preview-content {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 4px;
    border: 1px solid #EBEEF5;
    box-shadow: 1px 2px 10px #eee;
    background-color: #fff;
  }
  .preview-title {
    margin: 0;
    padding-top: 15px;
    padding-bottom: 10px;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .preview-img {
    width: 100%;
  }
}
</style>
