<template>
  <div v-if="selectComp" class="order-page">
    <vxe-modal v-model="typeVisible" title="支付方式" width="480" destroy-on-close>
      <template v-slot>
        <div class="pro-pays">
          <vxe-radio-group v-model="payType">
          <vxe-radio label="zfb" v-if="selectComp.pays.includes('zfb')"><img class="pay-icon" src="../assets/image/support/alipay_icon.png" width="26">支付宝</vxe-radio>
          <vxe-radio label="wx" v-if="selectComp.pays.includes('wx')"><img class="pay-icon" src="../assets/image/support/wechat_icon.png" width="26">微信</vxe-radio>
        </vxe-radio-group>
        </div>
        <div class="pro-btns">
          <vxe-button status="primary" :disabled="!payType" @click="pay()">下一步</vxe-button>
        </div>
      </template>
    </vxe-modal>

    <vxe-modal v-model="qqVisible" title="支持作者" height="200" width="500" destroy-on-close>
      <template v-slot>
        <vxe-form ref="qqForm" :data="qqFormData" :rules="qqFormRules" @submit="vaildQQ" title-width="90" title-align="right" title-colon>
          <vxe-form-item title="支付方式">
            <template v-slot>
              <vxe-radio-group v-model="payType">
                <vxe-radio label="zfb" v-if="selectComp.pays.includes('zfb')"><img class="pay-icon" src="../assets/image/support/alipay_icon.png" width="26">支付宝</vxe-radio>
                <vxe-radio label="wx" v-if="selectComp.pays.includes('wx')"><img class="pay-icon" src="../assets/image/support/wechat_icon.png" width="26">微信</vxe-radio>
              </vxe-radio-group>
            </template>
          </vxe-form-item>
          <vxe-form-item field="qq" title="QQ号">
            <template v-slot="scope">
              <vxe-input type="integer" ref="email" v-model="qqFormData.qq" :controls="false" placeholder="请输入QQ号" clearable @input="$refs.qqForm.updateStatus(scope)"></vxe-input>
              <vxe-button status="primary" type="submit" :disabled="!qqFormData.qq">下一步</vxe-button>
            </template>
          </vxe-form-item>
        </vxe-form>
      </template>
    </vxe-modal>

    <vxe-modal v-model="emailVisible" title="注册账号" height="340" width="500" destroy-on-close>
      <template v-slot>
        <vxe-form ref="emailForm" :data="emailFormData" :rules="emailFormRules" @submit="vaildEmail" title-width="120" title-align="right" title-colon>
          <vxe-form-item title="支付方式">
            <template v-slot>
              <vxe-radio-group v-model="payType">
                <vxe-radio label="zfb" v-if="selectComp.pays.includes('zfb')"><img class="pay-icon" src="../assets/image/support/alipay_icon.png" width="26">支付宝</vxe-radio>
                <vxe-radio label="wx" v-if="selectComp.pays.includes('wx')"><img class="pay-icon" src="../assets/image/support/wechat_icon.png" width="26">微信</vxe-radio>
              </vxe-radio-group>
            </template>
          </vxe-form-item>
          <vxe-form-item field="qq" title="QQ号" :title-suffix="{ message: '授权后，请使用该QQ号申请加入支持群！', icon: 'fa fa-exclamation-circle' }">
            <template v-slot="scope">
              <vxe-input ref="qq" type="integer" v-model="emailFormData.qq" placeholder="加入支持群的QQ号" :controls="false" clearable @input="$refs.emailForm.updateStatus(scope)"></vxe-input>
            </template>
          </vxe-form-item>
          <vxe-form-item field="name" :title="selectComp.authUserType === '1' ? '公司名称' : '个人姓名'" :title-suffix="{ message: '被授权方确认后不可更改，需谨慎填写！', icon: 'fa fa-exclamation-circle' }">
            <template v-slot="scope">
              <vxe-input ref="name" v-model="emailFormData.name" placeholder="请输入被授权方" clearable @input="$refs.emailForm.updateStatus(scope)"></vxe-input>
            </template>
          </vxe-form-item>
          <vxe-form-item field="email" title="邮箱" :title-suffix="{ message: '该邮箱用于接收授权相关信息！', icon: 'fa fa-exclamation-circle' }">
            <template v-slot="scope">
              <vxe-input ref="email" v-model="emailFormData.email" placeholder="请输入邮箱" clearable @input="$refs.emailForm.updateStatus(scope)"></vxe-input>
              <vxe-button @click="getEmail" :disabled="triggerEmail || emailDownTime > 0">{{ checkEmail ? '获取验证码' : (emailDownTime > 0 ? emailDownTime + '秒后重新获取' : '重新获取') }}</vxe-button>
            </template>
          </vxe-form-item>
          <vxe-form-item field="code" title="验证码" :item-render="{autofocus: '.vxe-input--inner'}">
            <template v-slot="scope">
              <vxe-input v-model="emailFormData.code" placeholder="请输入验证码" clearable @input="$refs.emailForm.updateStatus(scope)"></vxe-input>
              <vxe-button status="primary" type="submit" :disabled="!emailFormData.email">下一步</vxe-button>
            </template>
          </vxe-form-item>
        </vxe-form>
      </template>
    </vxe-modal>

    <vxe-modal v-model="payVisible" :loading="payLoading" :show-header="false" width="520" height="500">
      <template v-slot>
        <div class="pay-popup" v-if="selectPay">
          <div class="pay-desc">
            <template v-if="payType === 'zfb'">
              <img class="pay-icon" src="../assets/image/support/alipay_icon.png">
            </template>
            <template v-else-if="payType === 'wx'">
              <img class="pay-icon" src="../assets/image/support/wechat_icon.png">
            </template>
            <span>{{ $utils.getPayType(payType) }}扫码支付</span>
          </div>
          <div class="pay-qr">
            <div class="pay-left">
              <img :src="qrUrl">
            </div>
            <div class="pay-right">
              <div class="pay-price">
                <span class="price-unit">￥{{ selectPay.amount }}</span>
              </div>
              <div class="pay-down">
                <div class="pay-msg">请在有效时间内完成付款<br>超时将自动取消</div>
                <div class="pay-time">{{ payCountdown }}</div>
              </div>
              <div class="pay-oper">
                <vxe-button status="info" @click="cancelPay" size="mini">取消付款</vxe-button>
              </div>
            </div>
          </div>
          <div class="pay-name">
            <span class="bold">产品名称：</span>
            <span>{{ selectPay.appCode }}</span>
          </div>
          <div class="pay-no">
            <span class="bold">订单单号：</span>
            <span>{{ selectPay.orderNo }}</span>
          </div>
          <div class="pay-type">
            <span class="bold">支付方式：</span>
            <span>{{ $utils.getPayType(payType) }}</span>
          </div>
          <div class="pay-name" v-if="!['SupportQQ'].includes(selectComp.code)">
            <span class="bold">被授权方：</span>
            <span>{{ selectPay.name || '-' }}</span>
          </div>
          <div class="pay-name" v-if="!['SupportQQ'].includes(selectComp.code)">
            <span class="bold">邮箱账号：</span>
            <span>{{ selectPay.email || '-' }}</span>
          </div>
          <div class="pay-name">
            <span class="bold"> Q Q 号：</span>
            <span>{{ selectPay.qq || '-' }}</span>
          </div>
        </div>
      </template>
    </vxe-modal>

    <vxe-modal v-model="proVisible" :loading="proLoading" :title="`${selectComp.name} - 支付`" width="500" height="440" esc-closable>
      <template v-slot>
        <div v-if="proPrompt">
          <div class="pro-prompt">
            <div>请先联系 <qq-service></qq-service> 咨询</div>
            <div class="pp-btns">
              <vxe-button status="primary" content="已确定，点击付款" @click="proPrompt = false"></vxe-button>
            </div>
          </div>
        </div>
        <div v-else>
          <div class="pro-pays">
            <vxe-radio-group v-model="payType">
              <vxe-radio label="zfb" v-if="selectComp.pays.includes('zfb')"><img class="pay-icon" src="../assets/image/support/alipay_icon.png" width="26">支付宝</vxe-radio>
              <vxe-radio label="wx" v-if="selectComp.pays.includes('wx')"><img class="pay-icon" src="../assets/image/support/wechat_icon.png" width="26">微信</vxe-radio>
            </vxe-radio-group>
          </div>
          <div class="pro-amoun">
            <div class="comp-amoun">￥{{ selectComp.amount }}</div>
          </div>
          <div class="pro-qr" :style="{visibility: proLoading ? 'hidden' : 'visible'}">
            <img :src="`${apiURL}/vxe/api/qr/${code}_${payType}.png`" height="200">
          </div>
          <div class="pro-desc">
            <span class="red">（注：付款完成后将交易订单号发给 <qq-service></qq-service> 即可）</span>
          </div>
        </div>
      </template>
    </vxe-modal>

    <vxe-modal v-model="supportVisible" title="感谢您对 vxe-table 的支持" width="440" height="300" esc-closable>
      <template v-slot>
        <div style="text-align: center;">
          <div>
            <img src="../assets/image/support/qq.png" style="width: 189px; height: 183px">
          </div>
          <div>扫码加入QQ群：652309891</div>
        </div>
      </template>
    </vxe-modal>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
import XEAjax from 'xe-ajax'
import XEUtils from 'xe-utils'
import QRCode from 'qrcode'

const winAlert = alert

export default {
  name: 'CompOrder',
  props: {
    code: String
  },
  data () {
    return {
      proPrompt: true,
      proVisible: false,
      proLoading: false,
      payType: 'zfb',

      emailVisible: false,
      checkEmail: true,
      triggerEmail: false,
      emailFormData: {
        name: '',
        email: '',
        qq: '',
        code: ''
      },
      emailFormRules: {
        name: [
          {
            required: true,
            validator: ({ itemValue }) => {
              if (this.selectComp.authUserType === '1') {
                if (!itemValue) {
                  return new Error('请输入正确的公司完整名称')
                }
              } else {
                if (!itemValue || /(公司)|(企业)|(有限)/.test(itemValue)) {
                  return new Error('请输入的个人姓名')
                }
              }
            }
          }
        ],
        email: [
          { required: true, pattern: /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/, message: '请输入有效的邮箱账号' }
        ],
        code: [
          { required: true, message: '请输入验证码' }
        ]
      },

      qqVisible: false,
      qqFormData: {
        qq: ''
      },
      qqFormRules: {
        qq: [
          { required: true, pattern: /^[1-9][0-9]{4,12}$/, message: '请输入有效的QQ号' }
        ]
      },

      supportVisible: false,
      typeVisible: false,

      payVisible: false,
      payLoading: false,
      selectPay: null,
      qrUrl: '',
      payCountdown: '',
      emailDownTime: 0
    }
  },
  computed: {
    ...mapState([
      'apiURL',
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  },
  destroyed () {
    clearTimeout(this.edTimeout)
    this.proVisible = false
    this.payVisible = false
  },
  methods: {
    ...mapMutations([
      'updateLoginInfo',
      'openRegInfo'
    ]),
    open () {
      clearTimeout(this.edTimeout)
      this.proPrompt = true
      this.checkEmail = true
      this.triggerEmail = false
      this.qqVisible = false
      this.proVisible = false
      this.supportVisible = false
      this.typeVisible = false
      this.emailDownTime = 0
      if (this.selectComp.pays && this.selectComp.pays.length) {
        if (!this.selectComp.pays.includes(this.payType)) {
          this.payType = this.selectComp.pays[0]
        }
      } else {
        this.payType = ''
      }
      XEUtils.clear(this.emailFormData, '')
      this.updateLoginInfo()
      if (['SupportQQ'].includes(this.code)) {
        this.qqVisible = true
      } else {
        this.proVisible = false
        if (this.loginToken && this.loginInfo) {
          this.typeVisible = true
        } else {
          this.$XModal.confirm({
            escClosable: true,
            content: '当前未登录，是否登录！',
            title: '登录提示',
            cancelButtonText: '注册一个',
            confirmButtonText: '马上登录'
          }).then(type => {
            if (type === 'confirm') {
              document.getElementById('login').click()
            } else if (type === 'cancel') {
              this.emailVisible = true
            }
          })
        }
      }
    },
    startDownTime () {
      if (this.emailVisible) {
        this.edTimeout = setTimeout(() => {
          this.emailDownTime -= 1
          if (this.emailDownTime > 0) {
            this.startDownTime()
          }
        }, 1000)
      }
    },
    getEmail () {
      if (!this.emailFormData.email) {
        this.$refs.email.focus()
        return
      }
      if (this.emailDownTime <= 0) {
        this.checkEmail = true
        this.triggerEmail = true
        XEAjax.post('/vxe/api/pub/vxetable/check/email', {
          email: this.emailFormData.email
        }).then(data => {
          const rest = this.$utils.getRest(data)
          if (rest && rest.result && rest.result.email) {
            this.$XModal.message({
              content: '验证码发送成功',
              status: 'success'
            })
            this.checkEmail = false
            this.triggerEmail = false
            this.emailFormData.code = ''
            this.emailDownTime = rest.result.ss * 60
            this.startDownTime()
          } else {
            this.triggerEmail = false
            if (rest.result && rest.result.downTime) {
              this.checkEmail = false
              this.emailDownTime = rest.result.downTime
              this.startDownTime()
            }
            this.$XModal.message({
              content: rest.message,
              status: 'error'
            })
          }
        })
      }
    },
    vaildEmail () {
      if (!this.emailFormData.email) {
        this.$XModal.message({
          content: '邮箱不能为空',
          status: 'error'
        })
        return
      }
      if (!this.emailFormData.code) {
        this.$XModal.message({
          content: '验证码不能为空',
          status: 'error'
        })
        return
      }
      XEAjax.post('/vxe/api/pub/vxetable/vaild/email', this.emailFormData).then(data => {
        const rest = this.$utils.getRest(data)
        if (rest && rest.result && rest.result.email) {
          this.checkEmail = false
          this.pay({ email: rest.result.email, qq: rest.result.qq, name: rest.result.name })
        } else {
          this.$XModal.message({
            content: rest.message,
            status: 'error'
          })
        }
      })
    },
    vaildQQ () {
      if (!this.qqFormData.qq) {
        this.$XModal.message({
          content: 'QQ不能为空',
          status: 'error'
        })
        return
      }
      this.pay({ qq: this.qqFormData.qq })
    },
    cancelPay () {
      this.$XModal.confirm({
        content: '是否取消付款？',
        title: '授权提示'
      }).then(type => {
        if (type === 'confirm') {
          this.payVisible = false
          this.updateLoginInfo()
        }
      })
    },
    pay ({ email, qq, name } = {}) {
      this.qqVisible = false
      this.emailVisible = false
      this.payVisible = true
      this.payLoading = true
      this.typeVisible = false
      this.qrUrl = ''
      this.payCountdown = ''
      this.selectPay = null
      XEAjax.post(`/vxe/api/auth/pay/${this.payType}/create/${this.code}`, {
        qq,
        email,
        name
      }).then(data => {
        const rest = this.$utils.getRest(data)
        this.payLoading = false
        if (rest && rest.result) {
          this.selectPay = rest.result
          this.getAlipayStatus().then((rest) => {
            this.payVisible = false
            this.updateLoginInfo()
            if (rest && rest.status === 'success') {
              if (this.code === 'SupportQQ') {
                this.supportVisible = true
              } else {
                this.$XModal.alert({
                  content: '授权成功，授权信息已发送到邮箱中，请注意查收！',
                  title: '支付成功',
                  status: 'success'
                })
              }
            } else if (rest && rest.status === 'cancel') {
            } else {
              this.$XModal.alert({
                content: '该订单支付超时，本次支付已取消！',
                title: '支付超时',
                status: 'warning'
              })
            }
          }).catch(() => {
            this.payVisible = false
            this.updateLoginInfo()
            this.$XModal.alert({
              content: '授权失败，若该订单已支付成功，则在7个工作日内退回',
              title: '支付失败',
              status: 'error'
            })
          })
          QRCode.toDataURL(rest.result.qrCode, { width: 200 }).then(url => {
            this.qrUrl = url
          })
        } else {
          this.payVisible = false
          winAlert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
        }
      }).catch((data) => {
        const rest = this.$utils.getRest(data)
        this.payLoading = false
        this.payVisible = false
        if ([9, 19].includes(rest.code)) {
          this.openRegInfo()
        } else {
          this.$XModal.message({
            content: rest && rest.message ? rest.message : decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'),
            status: 'error'
          })
        }
        this.updateLoginInfo()
      })
    },
    getAlipayStatus () {
      let isOut = false
      const orderNo = this.selectPay.orderNo
      const timeout = XEUtils.toNumber(this.selectPay.timeout)
      const endDate = new Date(Date.now() + Math.max(1000, timeout - 1000))
      const outPromise = new Promise(resolve => {
        const updateDowntime = () => {
          if (this.payVisible) {
            const dateDiff = XEUtils.getDateDiff(new Date(), endDate)
            isOut = !dateDiff.done
            if (isOut) {
              resolve({
                status: 'timeout'
              })
            } else {
              this.payCountdown = `${dateDiff.mm}分${dateDiff.ss}秒`
              this.downTime = setTimeout(() => {
                if (this.payVisible) {
                  updateDowntime()
                }
              }, 1000)
            }
          }
        }
        updateDowntime()
      })
      return Promise.race([
        outPromise,
        new Promise((resolve, reject) => {
          const queryResult = () => {
            if (this.payVisible) {
              if (isOut) {
                resolve({
                  status: 'timeout'
                })
              } else {
                setTimeout(() => {
                  if (this.payVisible) {
                    XEAjax.get(`/vxe/api/auth/pay/query/${orderNo}`).then(data => {
                      const rest = this.$utils.getRest(data)
                      if (rest.result.status === 'success') {
                        resolve(rest.result)
                      } else if (rest.result.status === 'wait') {
                        queryResult()
                      } else {
                        resolve(rest.result)
                      }
                    }).catch(() => {
                      const rest = {
                        status: 'error'
                      }
                      reject(rest)
                    })
                  } else {
                    resolve({
                      status: 'cancel'
                    })
                  }
                }, 5000)
              }
            } else {
              resolve({
                status: 'cancel'
              })
            }
          }
          queryResult(orderNo)
        })
      ])
    }
  }
}
</script>

<style scoped lang="scss">
.pay-popup {
  padding: 20px 10px 20px 10px;
  .pay-name,
  .pay-no,
  .pay-type {
    line-height: 26px;
    padding-left: 15px;
  }
  .pay-desc {
    color: #409eff;
    text-align: center;
    padding: 5px 0;
    font-size: 18px;
    user-select: none;
    .pay-icon {
      width: 60px;
      margin-right: 10px;
    }
  }
  .pay-qr {
    display: flex;
    flex-direction: row;
    user-select: none;
    .pay-price {
      text-align: center;
      color: #ff6700;
      .price-unit {
        font-size: 32px;
      }
    }
    .pay-left {
      & > img {
        width: 200px;
        height: 200px;
      }
    }
    .pay-right {
      flex-grow: 1;
      padding-top: 15px;
    }
    .pay-msg {
      font-size: 14px;
      color: #000;
    }
    .pay-down {
      font-size: 40px;
      text-align: center;
      color: red;
    }
    .pay-oper {
      margin-top: 10px;
      text-align: center;
    }
  }
}
.pro-pays {
  text-align: center;
  margin: 15px 0;
}
.pro-btns {
  text-align: right;
}
.pro-qr {
  text-align: center;
  margin-bottom: 15px;
}
.pro-amoun {
  text-align: center;
  margin-top: 15px;
}
.pro-desc {
  padding: 5px 0;
  text-align: center;
}
.pro-prompt {
  padding-top: 40px;
  text-align: center;
  .pp-contact {
    display: inline-block;
    text-align: left;
  }
  .pp-btns {
    padding-top: 20px;
  }
}
</style>
