import Vue from 'vue'
import Vuex from 'vuex'
import XEUtils from 'xe-utils'
import XEAjax from 'xe-ajax'

Vue.use(Vuex)

function updateUserComp (state) {
  state.appCompList.forEach(gItem => {
    gItem.options = gItem._options.filter(item => item.isEnable && (!state.loginInfo || item.authUserType === state.loginInfo.aType))
    gItem.selected = gItem.options.length ? gItem.options[0].code : ''
  })
}

const store = new Vuex.Store({
  state: {
    apiURL: process.env.NODE_ENV === 'development' ? '' : 'https://api.xuliangzhan.com:10443',
    serveTime: Date.now(),
    loginStatus: 0,
    loginToken: sessionStorage.getItem('AUTH_TOKEN'),
    loginInfo: null,
    visibleDePawd: false,
    visibleRegInfo: false,
    appList: [],
    appCompList: []
  },
  mutations: {
    setServeTime (state, time) {
      state.serveTime = time
    },
    updateLoginInfo (state) {
      state.loginStatus++
    },
    setLoginToken (state, token) {
      state.loginToken = token
    },
    setLoginInfo (state, obj) {
      state.loginInfo = obj
      updateUserComp(state)
    },
    setDePawd (state, visible) {
      state.visibleDePawd = visible
    },
    openDePawd (state) {
      state.visibleDePawd = true
    },
    closeDePawd (state) {
      state.visibleDePawd = false
    },
    setRegInfo (state, visible) {
      state.visibleRegInfo = visible
    },
    openRegInfo (state) {
      state.visibleRegInfo = true
    },
    closeRegInfo (state) {
      state.visibleRegInfo = false
    },
    setAppList (state, list) {
      const compList = []
      list.forEach(item => {
        if (!['FilterCombination', 'FilterComplexInput'].includes(item.code)) {
          const [gCode] = item.code.split('_')
          let compItem = compList.find(gItem => gItem.gCode === gCode && gItem.gType === item.group)
          if (compItem) {
            compItem._options.push(item)
          } else {
            compItem = {
              gCode,
              gType: item.group,
              selected: '',
              _options: [item],
              options: []
            }
            compList.push(compItem)
          }
        }
      })
      state.appList = list
      state.appCompList = compList
      updateUserComp(state)
    }
  },
  actions: {
    clearUser ({ commit }) {
      commit('setLoginToken', null)
      commit('setLoginInfo', null)
      sessionStorage.removeItem('AUTH_TOKEN')
    },
    updateUser ({ dispatch, commit, state }) {
      const strs = 'ijkoefgcdlmnphzytwab'.split('')
      const params = {}
      XEUtils.sample(strs, 2).forEach(key => {
        params[key] = XEUtils.random(15, 999)
      })
      return XEAjax.get(`/vxe/api/pub/vxetable/vaild/${state.loginToken}`, params).then(data => {
        const rest = XEUtils.getRest(data)
        if (rest) {
          if (rest.code === 0 && XEUtils.isArray(rest.result.compList)) {
            commit('setLoginInfo', rest.result)
          } else {
            dispatch('clearUser')
          }
        }
      }).catch((data) => {
        const rest = XEUtils.getRest(data)
        if (rest && rest.code) {
          dispatch('clearUser')
        }
      })
    }
  },
  modules: {
  }
})

export default store
