<template>
  <div v-if="selectComp && authComp">
    <vxe-modal v-model="expressVisible" title="增值税普通发票" width="600" height="460" show-zoom resize esc-closable>
      <template v-if="authComp.isInvoice && applyFormData.expressName">
        <div>当前进度：<span class="green">发票已寄出</span></div>
        <div>邮寄单号：<span class="bold">{{ applyFormData.expressName }} {{ applyFormData.expressNo }}</span></div>
      </template>
      <template v-else>
        <div>当前进度：<span class="primary">处理中</span></div>
      </template>
      <div>收件信息：</div>
      <ul>
        <li>收件人：{{ applyFormData.expressBy }}</li>
        <li>手机号码：{{ applyFormData.expressPhone }}</li>
        <li>收件地址：{{ applyFormData.expressAddress }}</li>
      </ul>
      <div>发票信息：</div>
      <ul>
        <li>名称：{{ applyFormData.name }}</li>
        <li>税号：{{ applyFormData.dutyNo }}</li>
        <li>地址：{{ applyFormData.address }}</li>
        <li>联系电话：{{ applyFormData.phone }}</li>
        <li>开户银行：{{ applyFormData.bankName }}</li>
        <li>银行网点：{{ applyFormData.bankOutlets }}</li>
        <li>银行账号：{{ applyFormData.bankAccount }}</li>
      </ul>
      <div style="text-align: right;">如有疑问联系 <qq-service></qq-service> 咨询</div>
    </vxe-modal>

    <vxe-modal v-model="applyVisible" :loading="applyLoading" title="增值税普通发票" width="700" height="760" show-zoom resize>
      <vxe-form ref="xForm" :data="applyFormData" :rules="isEnterprise ? gsFormRules : grFormRules" title-width="100" title-align="right" @submit="submitApply">
        <vxe-form-item span="24" align="center" v-if="!loginInfo || !loginInfo.aType">
          <template #default>
            <vxe-radio-group v-model="applyFormData.userType" @change="typeChangeEvent">
              <vxe-radio label="1" content="企业"></vxe-radio>
              <vxe-radio label="3" content="个人"></vxe-radio>
            </vxe-radio-group>
          </template>
        </vxe-form-item>

        <vxe-form-item title="收件信息" span="24" :title-colon="false" :title-suffix="{message: '请填接收发票的收件人信息'}"></vxe-form-item>
        <vxe-form-item field="expressBy" title="收件人" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: '请输入收件人'}}"></vxe-form-item>
        <vxe-form-item field="expressPhone" title="手机号码" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: '请输入收件人手机号码'}}"></vxe-form-item>
        <vxe-form-item field="expressAddress" title="收件地址" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: '请输入收件人地址'}}"></vxe-form-item>

        <vxe-form-item title="发票信息" span="24" :title-colon="false" :title-suffix="{message: '请填发票的信息'}"></vxe-form-item>
        <vxe-form-item field="name" title="名称" span="24" :reset-value="loginInfo.aName" :item-render="{name: '$input', props: {clearable: true, disabled: !!loginInfo.aName, placeholder: isEnterprise ? '请输入公司名称' : '请输入姓名'}}"></vxe-form-item>
        <vxe-form-item field="dutyNo" title="税号" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: '请输入税号'}}"></vxe-form-item>
        <vxe-form-item field="address" title="地址" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: '请输入地址'}}" :visible-method="visibleMethod"></vxe-form-item>
        <vxe-form-item field="phone" title="联系电话" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: '请输入联系电话'}}" :visible-method="visibleMethod"></vxe-form-item>
        <!-- <vxe-form-item field="bankName" title="开户银行" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: '请输入开户银行'}}" :visible-method="visibleMethod"></vxe-form-item> -->
        <vxe-form-item field="bankOutlets" title="银行网点" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: '请输入银行网点'}}" :visible-method="visibleMethod"></vxe-form-item>
        <vxe-form-item field="bankAccount" title="银行账号" span="24" :item-render="{name: '$input', props: {clearable: true, placeholder: '请输入银行账号'}}" :visible-method="visibleMethod"></vxe-form-item>

        <vxe-form-item align="center" span="24">
          <template #default>
            <vxe-button type="submit" content="确认提交" status="primary"></vxe-button>
            <vxe-button type="reset" content="重置"></vxe-button>
          </template>
        </vxe-form-item>
      </vxe-form>
    </vxe-modal>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
import XEAjax from 'xe-ajax'
import XEUtils from 'xe-utils'

export default {
  name: 'InvoiceInfo',
  props: {
    code: String
  },
  data () {
    return {
      expressVisible: false,
      applyVisible: false,
      applyLoading: false,
      applyFormData: {
        userType: '1',
        name: '',
        dutyNo: '',
        address: '',
        phone: '',
        bankName: '',
        bankOutlets: '',
        bankAccount: '',

        expressName: '',
        expressNo: '',
        expressBy: '',
        expressPhone: '',
        expressAddress: ''
      },
      gsFormRules: {
        name: [
          { required: true, message: '公司名称必须填写' }
        ],
        dutyNo: [
          { required: true, message: '税号必须填写' }
        ],
        expressBy: [
          { required: true, message: '收件人必须填写' }
        ],
        expressPhone: [
          { required: true, message: '手机号码必须填写' }
        ],
        expressAddress: [
          { required: true, message: '收件地址必须填写' }
        ]
      },
      grFormRules: {
        name: [
          { required: true, message: '名称必须填写' }
        ],
        expressBy: [
          { required: true, message: '收件人必须填写' }
        ],
        expressPhone: [
          { required: true, message: '手机号码必须填写' }
        ],
        expressAddress: [
          { required: true, message: '收件地址必须填写' }
        ]
      }
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    isEnterprise () {
      return this.loginInfo.aType === '1'
    },
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList) {
        return this.loginInfo.compList.find(comp => comp.appCode === this.code)
      }
      return null
    },
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  },
  methods: {
    ...mapActions([
      'updateUser'
    ]),
    visibleMethod () {
      return this.isEnterprise
    },
    open () {
      this.expressVisible = true
      XEUtils.clear(this.applyFormData, '', { userType: this.loginInfo.aType || '1', name: this.loginInfo.aName })
      XEAjax.get(`/vxe/api/pub/vxetable/invoice/${this.code}`).then(data => {
        const rest = this.$utils.getRest(data)
        XEUtils.destructuring(this.applyFormData, rest ? rest.result : {})
        this.updateUser()
      })
    },
    submit () {
      this.applyVisible = true
      this.applyFormData.name = this.loginInfo.aName
      XEAjax.get(`/vxe/api/pub/vxetable/invoice/${this.code}`).then(data => {
        const rest = this.$utils.getRest(data)
        XEUtils.clear(this.applyFormData, '', { userType: this.loginInfo.aType || '1', name: this.loginInfo.aName })
        XEUtils.destructuring(this.applyFormData, rest ? rest.result : {})
        this.updateUser()
      }).catch(() => {
        XEUtils.clear(this.applyFormData, '', { userType: this.loginInfo.aType || '1', name: this.loginInfo.aName })
      })
    },
    submitApply () {
      this.applyLoading = true
      XEAjax.post(`/vxe/api/pub/vxetable/invoice/apply/${this.code}`, this.applyFormData).then(data => {
        const rest = this.$utils.getRest(data)
        this.applyLoading = false
        if (rest && rest.code === 0) {
          this.applyVisible = false
          this.$XModal.message({ content: rest.message, status: 'success' })
        } else {
          this.$XModal.message({ content: rest ? rest.message : '无权限申请，请稍后再试', status: 'error' })
        }
      }).catch((data) => {
        const rest = this.$utils.getRest(data)
        this.applyLoading = false
        this.$XModal.alert({ content: rest ? rest.message : '无权限操作，请稍后再试', status: 'error' })
      }).then(() => {
        this.updateUser()
      })
    },
    typeChangeEvent () {
      this.$refs.xForm.reset()
    }
  }
}
</script>
