import Vue from 'vue'
import XEUtils from 'xe-utils'
import {
  // 核心
  VXETable,

  // 表格功能
  Header,
  // Footer,
  Icon,
  // Filter,
  // Edit,
  // Menu,
  // Export,
  // Keyboard,
  // Validator,

  // 可选组件
  Column,
  // Colgroup,
  // Grid,
  Tooltip,
  // Toolbar,
  Pager,
  Form,
  FormItem,
  Checkbox,
  CheckboxGroup,
  Radio,
  RadioGroup,
  RadioButton,
  // Switch,
  Input,
  Select,
  Optgroup,
  Option,
  // Textarea,
  Button,
  Modal,
  // List,
  // Pulldown,

  // 表格
  Table
} from 'vxe-table'

import zhCNLocat from 'vxe-table/lib/locale/lang/zh-CN'

// 导入默认的国际化（如果项目中使用多语言，则应该导入到 vue-i18n 中）
VXETable.setup({
  i18n: (key, args) => XEUtils.toFormatString(XEUtils.get(zhCNLocat, key), args)
})

// 全局默认参数
VXETable.setup({
  version: 0,
  zIndex: 100
})

// 表格功能
Vue.use(Header)
  // .use(Footer)
  .use(Icon)
  // .use(Filter)
  // .use(Edit)
  // .use(Menu)
  // .use(Export)
  // .use(Keyboard)
  // .use(Validator)

  // 可选组件
  .use(Column)
  // .use(Colgroup)
  // .use(Grid)
  .use(Tooltip)
  // .use(Toolbar)
  .use(Pager)
  .use(Form)
  .use(FormItem)
  .use(Checkbox)
  .use(CheckboxGroup)
  .use(Radio)
  .use(RadioGroup)
  .use(RadioButton)
  // .use(Switch)
  .use(Input)
  .use(Select)
  .use(Optgroup)
  .use(Option)
  // .use(Textarea)
  .use(Button)
  .use(Modal)
  // .use(List)
  // .use(Pulldown)

  // 安装表格
  .use(Table)

// 安装表格
Vue.use(Table)

Vue.prototype.$XModal = VXETable.modal
