<template>
  <div class="pro-page" v-if="selectComp">
    <div class="pro-desc">
      <div class="pro-price">
        <template v-if="authComp">
          <span class="comp-auth">已获得授权</span>
          <vxe-button class="btn" status="primary" size="mini" @click="$router.replace({name: 'ProExtension'})" style="margin-left: 15px;">查看详情</vxe-button>
        </template>
        <template v-else>
          <vxe-select class="comp-name" v-model="selectGroupComp.selected" :options="selectGroupComp.options" :option-props="{label: 'name', value: 'code'}" size="mini"></vxe-select>
          <span class="comp-type"> - {{ $utils.getCompType(selectComp.authCodeType) }}</span>
          <template v-if="selectComp.pricing !== selectComp.amount">
            <span class="comp-pricing">￥{{ selectComp.pricing }}</span>
            <span class="comp-amoun">￥{{ selectComp.amount }}</span>
          </template>
          <template v-else>
            <span class="comp-amoun">￥{{ selectComp.amount }}</span>
          </template>
          <span class="comp-expire">/ {{ $utils.getCompExpiration(selectComp.deadline) }}</span>
          <vxe-button class="btn" status="danger" size="mini" @click="$router.replace({name: 'ProExtension'})" style="margin-left: 15px;">获取授权</vxe-button>
        </template>
        <template v-if="!authComp || !$utils.isWithin30Day(authComp.authDate)">
          <vxe-button class="btn" status="primary" type="text" @click="$refs.releaseLog.open()">{{ authComp ? 'v3 版本' : 'v3 版本' }}</vxe-button>
          <vxe-button class="btn" status="primary" type="text" @click="$refs.releaseNextLog.open()">{{ authComp ? 'v4 版本' : 'v4 版本' }}</vxe-button>
        </template>
      </div>
    </div>
    <div class="pro-preview">
      <div class="preview-item" v-for="item in proFeatureList" :key="item.code">
        <div class="preview-content">
          <h3 class="preview-title">{{ item.title }}</h3>
          <img class="preview-img" :src="item.previewUrl">
        </div>
      </div>
    </div>
    <release-log ref="releaseLog" :code="selectComp.code"></release-log>
    <release-log ref="releaseNextLog" :code="selectComp.code" :release-code="`${selectComp.code}_Next`"></release-log>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      version: '1.0.12',
      proPrice: 1200,
      authTypeList: [
        { value: 'release', label: '压缩版' }
      ],
      proFeatureList: [
        {
          title: '鼠标选取指定区域',
          code: 'proArea',
          previewUrl: require('../assets/image/plugin/proArea.gif')
        },
        {
          title: '多重区域选取',
          code: 'proMultiArea',
          previewUrl: require('../assets/image/plugin/proMultiArea.gif')
        },
        {
          title: '扩大区域选取范围',
          code: 'proAreaExtends',
          previewUrl: require('../assets/image/plugin/proAreaExtends.gif')
        },
        {
          title: '拖动扩展区域复制单元格值',
          code: 'proAreaExtendCopy',
          previewUrl: require('../assets/image/plugin/proAreaExtendCopy.gif')
        },
        {
          title: '拖动扩展区域单元格数值自增',
          code: 'proAreaExtendCalc',
          previewUrl: require('../assets/image/plugin/proAreaExtendCalc.gif')
        },
        {
          title: '固定列区域选取',
          code: 'proFixedAera',
          previewUrl: require('../assets/image/plugin/proFixedAera.gif')
        },
        {
          title: '复制、剪贴、粘贴',
          code: 'proClip',
          previewUrl: require('../assets/image/plugin/proClip.gif')
        },
        {
          title: '自动根据粘贴的内容自增行与列',
          code: 'proPasteIncrement',
          previewUrl: require('../assets/image/plugin/proPasteIncrement.gif')
        },
        {
          title: '合并、取消合并',
          code: 'proMerge',
          previewUrl: require('../assets/image/plugin/proMerge.gif')
        },
        {
          title: '查找和替换',
          code: 'proFNR',
          previewUrl: require('../assets/image/plugin/proFNR.gif')
        },
        {
          title: '常用的快捷键',
          code: 'proKeyboard',
          previewUrl: require('../assets/image/plugin/proKeyboard.gif')
        },
        {
          title: '右键快捷菜单',
          code: 'proContextmenu',
          previewUrl: require('../assets/image/plugin/proContextmenu.gif')
        },
        {
          title: '支持 echarts 图表渲染',
          code: 'proCharts',
          previewUrl: require('../assets/image/plugin/proCharts.gif')
        },
        {
          title: '同时支持区域选取、虚拟滚动',
          code: 'proVirtualScroll',
          previewUrl: require('../assets/image/plugin/proVirtualScroll.gif')
        }
      ]
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList',
      'appCompList'
    ]),
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList && this.selectGroupComp) {
        return this.loginInfo.compList.find(comp => this.selectGroupComp.options.some(item => item.code === comp.appCode))
      }
      return null
    },
    selectGroupComp () {
      return this.appCompList.find(item => item.gCode === 'PRO')
    },
    selectComp () {
      const gItem = this.selectGroupComp
      if (gItem) {
        return gItem.options.find(item => item.code === gItem.selected)
      }
      return null
    }
  }
}
</script>

<style lang="scss" scoped>
.price-unit {
  font-size: 18px;
  color: #ff6700;
  padding: 0 5px;
  font-weight: 700;
}
.pro-desc {
  position: relative;
  width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
}
.pro-price {
  padding: 15px;
  border-radius: 4px;
  border: 1px solid #EBEEF5;
  box-shadow: 1px 2px 10px #eee;
  background-color: #fff;
}
.comp-auth {
  font-size: 13px;
}
.help-icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  font-size: 12px;
  border-radius: 50%;
  margin: 0 5px;
  color: #fff;
  background-color: #000;
  text-align: center;
  cursor: help;
}
.pro-preview {
  width: 1200px;
  margin: 0 auto;
  &:after {
    content: "";
    display: block;
    clear: both;
    height: 0;
    overflow: hidden;
    visibility: hidden;
  }
  .preview-item {
    width: 600px;
    height: 420px;
    float: left;
    width: 50%;
    padding: 10px;
  }
  .preview-content {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 4px;
    border: 1px solid #EBEEF5;
    box-shadow: 1px 2px 10px #eee;
    background-color: #fff;
  }
  .preview-title {
    margin: 0;
    padding-top: 15px;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .preview-img {
    width: 100%;
  }
}
</style>
