<template>
  <div class="auth-agreement">
    <vxe-modal vxe-modal v-model="visible" title="插件协议" width="800" resize show-zoom esc-closable>
      <div v-if="selectComp" class="at-modal">
        <h4 class="at-title">协议事项</h4>
        <ul class="at-lic">
          <li v-if="selectComp.authUserType === '1'"><span class="bold">企业授权</span>：只限于在本企业生产的产品（内部、商用项目）上使用</li>
          <li v-else><span class="bold">个人授权</span>：只限于在个人生产的产品（个人商用项目）上使用</li>
          <li>不限制项目及部署数量</li>
          <template v-if="selectComp.supportDay > 0">
            <li>限制 1 人加入QQ官方支持群（提供 1 年技术支持）</li>
          </template>
          <template v-if="['PRO_Enterprise', 'PRO_Personal'].includes(code)">
            <li>授权成功后，您将会收到商用授权协议书.pdf（获得一个授权码）</li>
          </template>
          <li>
            <span class="forbid">禁止用于开源项目</span>
          </li>
          <li>
            <span class="forbid">禁止分享插件代码</span>
          </li>
          <li>
            <span class="forbid">禁止将代码进行二次销售</span>
          </li>
          <li>
            <span class="forbid">禁止用于类似产品和存在竞争性质的项目中</span>
          </li>
        </ul>
        <h4 class="at-title">常见问题</h4>
        <div class="at-issue">
          <template v-if="selectComp.invoice">
            <div class="at-issue-title">可以开发票吗？</div>
            <ul>
              <li>企业授权可以支持开发票</li>
            </ul>
          </template>
          <div class="at-issue-title">插件如何使用，是否影响之前的用法？</div>
          <ul>
            <li>如果是按照官网文档的用法，99% 完全兼容（如有特殊的兼容性用法需自行改动），原先项目代码无需任何改动，只需额外引入插件即可。</li>
          </ul>
          <div class="at-issue-title">支持 vxe-table 版本要求？</div>
          <ul>
            <li>支持 v2（2.10+）、v3（3.1+）、v4（4.0+）</li>
          </ul>
          <div class="at-issue-title">支持哪些浏览器？</div>
          <ul>
            <li>支持主流的现代浏览器，例如 Chrome、Firefox、Safari、Edge、搜狗、QQ、360 等浏览器。v2、v3 最低兼容 IE11，v4 不支持 IE。</li>
          </ul>
          <div class="at-issue-title">是否有 Bug？</div>
          <ul>
            <li>任何项目都不能保证没有 BUG，但我们会及时更新修复。</li>
          </ul>
          <div class="at-issue-title">授权时间是多久吗？</div>
          <ul>
            <li>永久商用授权，在所支持的版本内永久更新。</li>
          </ul>
        </div>
        <div class="at-contact">如有疑问联系 <qq-service></qq-service> 咨询</div>
      </div>
    </vxe-modal>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'AuthAgreement',
  props: {
    code: String
  },
  data () {
    return {
      visible: false
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  },
  methods: {
    open () {
      this.visible = true
    }
  }
}
</script>

<style lang="scss" scoped>
.at-modal {
  padding: 0 10px 10px 10px;
}
.at-issue {
  padding-left: 15px;
  ul {
    margin: 5px 0 15px 0;
  }
}
.at-contact {
  margin-top: 15px;
  text-align: right;
}
</style>
