<template>
  <div v-if="selectComp" class="release-page">
    <vxe-modal v-model="visible" :loading="loading" :title="selectComp.name" width="800" height="600" esc-closable resize show-zoom>
      <div v-for="(item, index) in releaseList" :key="index">
        <div class="log-version">
          <span class="log-name">v{{ item.version }}</span>
          <span class="log-date">{{ item.date }}</span>
          <a v-if="hasCompPrivilege && loginInfo.d === 'on' && index < 5" class="link download-lib" @click="downloadEvent(item.version)" :title="`点击下载 v${item.version}`">下载</a>
        </div>
        <div class="log-desc">
          <ul class="log-list">
            <li class="log-li" v-for="(log, cIndex) in item.logs" :key="`${index}${cIndex}`">{{ log }}</li>
          </ul>
        </div>
      </div>
    </vxe-modal>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
import XEAjax from 'xe-ajax'
import XEUtils from 'xe-utils'
import PROLog from '../common/releases/PRO'
import PRONextLog from '../common/releases/PRONext'
import FilterCombinationLog from '../common/releases/FilterCombination'
import FilterCombinationNextLog from '../common/releases/FilterCombinationNext'
import FilterComplexInputLog from '../common/releases/FilterComplexInput'
import FilterComplexInputNextLog from '../common/releases/FilterComplexInputNext'

const invalidCode = []

const winAlert = alert

export default {
  name: 'ReleaseLog',
  props: {
    code: String,
    releaseCode: String
  },
  data () {
    return {
      visible: false,
      loading: false,
      releaseMaps: {
        PRO: PROLog,
        PRO_Enterprise: PROLog,
        PRO_Personal: PROLog,
        PRO_Next: PRONextLog,
        PRO_Enterprise_Next: PRONextLog,
        PRO_Personal_Next: PRONextLog,

        FilterCombination: FilterCombinationLog,
        FilterCombination_Enterprise: FilterCombinationLog,
        FilterCombination_Personal: FilterCombinationLog,
        FilterCombination_Next: FilterCombinationNextLog,
        FilterCombination_Enterprise_Next: FilterCombinationNextLog,
        FilterCombination_Personal_Next: FilterCombinationNextLog,

        FilterComplexInput: FilterComplexInputLog,
        FilterComplexInput_Enterprise: FilterComplexInputLog,
        FilterComplexInput_Personal: FilterComplexInputLog,
        FilterComplexInput_Next: FilterComplexInputNextLog,
        FilterComplexInput_Enterprise_Next: FilterComplexInputNextLog,
        FilterComplexInput_Personal_Next: FilterComplexInputNextLog
      }
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    },
    releaseList () {
      return this.releaseMaps[this.releaseCode || this.code] || []
    },
    hasCompPrivilege () {
      return this.loginToken && this.loginInfo && this.loginInfo.compList && this.loginInfo.compList.find(comp => comp.appCode === this.code)
    }
  },
  methods: {
    ...mapMutations([
      'openDePawd',
      'openRegInfo'
    ]),
    open () {
      this.visible = true
    },
    downloadEvent (version) {
      const compName = this.code
      const loginInfo = this.loginInfo
      const loginToken = this.loginToken
      if (loginInfo && loginToken) {
        if (!invalidCode.includes(`${compName}_${version}_${loginToken}`)) {
          const strs = 'wabpcdoefghijklmnzyt'.split('')
          const params = {}
          XEUtils.sample(strs, 6).forEach(key => {
            params[key] = XEUtils.random(10, 999)
          })
          this.loading = true
          XEAjax.fetchPost(`/vxe/api/pub/vxetable/download/${compName}@${encodeURIComponent(version)}`, {
            n: Date.now(),
            k: loginInfo.key
          }, { params }).then(response => {
            if (response.status === 200) {
              response.blob().then(blob => {
                const a = document.createElement('a')
                a.target = '_blank'
                a.download = `${compName}_v${version}.rar`
                a.href = URL.createObjectURL(blob)
                document.body.appendChild(a)
                a.click()
                document.body.removeChild(a)
              })
            } else {
              response.json().then(data => {
                const rest = this.$utils.getRest(data)
                if (rest) {
                  if ([1, 99].includes(rest.code)) {
                    this.$XModal.confirm({
                      content: rest && rest.message ? rest.message : decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'),
                      title: '操作失败',
                      status: 'error',
                      confirmButtonText: '去修改'
                    }).then((type) => {
                      if (type === 'confirm') {
                        this.openDePawd()
                      }
                    })
                    return
                  } else if (rest.code === 2) {
                    this.openRegInfo()
                    return
                  }
                  if (rest.code > 10) {
                    invalidCode.push(`${compName}_${version}_${loginToken}`)
                  }
                  winAlert(rest.message)
                } else {
                  winAlert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
                }
              })
            }
            this.loading = false
          })
        } else {
          winAlert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
        }
      } else {
        winAlert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.release-page {
  .release-log-box {
    .popup-header {
      height: 40px;
      .popup-title {
        font-size: 18px;
        font-weight: 700;
      }
      .close-btn {
        position: absolute;
        right: 10px;
        top: 10px;
        height: 30px;
        width: 30px;
        text-align: center;
        border-radius: 50%;
        color: #000;
        font-size: 20px;
        user-select: none;
        cursor: pointer;
        &:hover {
          color: #409eff;
        }
      }
    }
    .popup-body {
      width: 600px;
      height: 400px;
      overflow: auto;
    }
  }
  .log-name {
    font-size: 22px;
    font-weight: 700;
    color: #0d1a26;
  }
  .log-date {
    font-size: 12px;
    margin-left: 15px;
    padding: 2px 5px;
    background: #f2f4f5;
    border: 1px solid #eee;
  }
  .log-list {
    .log-li {
      list-style-type: circle;
    }
  }
}
</style>
