import XEUtils from 'xe-utils'

import store from '../../store'

export function getRest (data) {
  if (data && data.x && XEUtils.isString(data.x)) {
    return XEUtils.toStringJSON(decodeURIComponent(data.x))
  }
  return data
}

export default {
  getRest,
  getCompType (type) {
    return type === 'src' ? '源码版' : '压缩版'
  },
  getPayType (payType) {
    switch (payType) {
      case 'zfb': return '支付宝'
      case 'wx': return '微信'
    }
    return ''
  },
  getCompExpiration (deadline) {
    return deadline > 0 ? '1年' : '永久'
  },
  getAuthUserType (authType) {
    switch (authType) {
      case '1': return '企业'
      case '2': return '团队'
      case '3': return '个人'
    }
    return '个人'
  },
  getAuthUserIcon (authType) {
    switch (authType) {
      case '1': return 'fa-tags'
      case '2': return 'fa-users'
      case '3': return 'fa-user'
    }
    return 'fa-exclamation'
  },
  isWithin30Day (date) {
    return XEUtils.toStringDate(date).getTime() >= store.state.serveTime - (1000 * 60 * 60 * 24 * 7)
  }
}
