<template>
  <div v-if="selectComp" class="page-view">
    <div class="comp-tip">
      <div>
        <template v-if="authComp">
          <span class="comp-name">{{ selectComp.name }}</span>
          <template v-if="authComp.isInvoice">
            <vxe-button type="text" class="invoice-btn" status="primary" @click="$refs.invoiceInfo.open()" content="查看发票"></vxe-button>
          </template>
          <template v-else-if="selectComp.invoice">
            <span v-if="$utils.isWithin30Day(authComp.authDate)" class="comp-invoice">
              <vxe-button type="text" class="invoice-btn" status="primary" @click="$refs.invoiceInfo.submit()" content="申请发票"></vxe-button>
            </span>
          </template>
        </template>
        <template v-else>
          <vxe-select class="comp-name" v-model="selectGroupComp.selected" :options="selectGroupComp.options" :option-props="{label: 'name', value: 'code'}" size="mini"></vxe-select>
          <span class="comp-type"> - {{ $utils.getCompType(selectComp.authCodeType) }}</span>
          <template v-if="selectComp.pricing !== selectComp.amount">
            <span class="comp-pricing">￥{{ selectComp.pricing }}</span>
            <span class="comp-amoun">￥{{ selectComp.amount }}</span>
          </template>
          <template v-else>
            <span class="comp-amoun">￥{{ selectComp.amount }}</span>
          </template>
          <span class="comp-expire">/ {{ $utils.getCompExpiration(selectComp.deadline) }}</span>
        </template>
      </div>
      <template v-if="loginInfo && authComp">
        <ul>
          <li>授权方式：<span class="green bold">{{ $utils.getCompType(authComp.authType) }}</span></li>
          <li>授权时间：<span class="green bold">{{ $utils.getCompExpiration(authComp.deadline) }}</span></li>
        </ul>
      </template>
      <template v-else>
        <ul>
          <li>{{ selectComp.authCodeType === 'src' ? '该授权包含完整源码' : '该授权不提供源码'}}</li>
        </ul>
      </template>
      <div class="comp-agreement">
        <a class="link" @click="$refs.authAgreement.open()">查看详细协议</a>
      </div>
      <div class="comp-oper">
        <vxe-button class="btn" status="danger" @click="$refs.compOrder.open()" v-if="!authComp">购买授权</vxe-button>
        <vxe-button class="btn" status="primary" @click="$refs.releaseLog.open()">{{ authComp ? '下载 v3.0 版本' : '查看 v3.0 版本' }}</vxe-button>
        <vxe-button class="btn" status="primary" @click="$refs.releaseNextLog.open()">{{ authComp ? '下载 v4.0 版本' : '查看 v4.0 版本' }}</vxe-button>
        <vxe-button class="btn" @click="$router.replace({name: backName})">返回</vxe-button>
      </div>
    </div>
    <div class="comp-preview">
      <div class="preview">
        <img :src="imgUrl">
      </div>
      <ul class="features">
        <li v-for="(item, index) in selectComp.features" :key="index">{{ item }}</li>
      </ul>
    </div>
    <div class="comp-api">
      <a class="link" href="/vxe-table/#/table/renderer/filter" target="_black">查看筛选渲染器的文档</a>
    </div>
    <div class="comp-desc">
      <pre>
        <pre-code class="text">{{ apiDesc[0] }}</pre-code>
      </pre>
    </div>
    <pre>
      <pre-code class="html">{{ demoCodes[0] }}</pre-code>
      <pre-code class="javascript">{{ demoCodes[1] }}</pre-code>
    </pre>
    <release-log ref="releaseLog" :code="selectComp.code"></release-log>
    <release-log ref="releaseNextLog" :code="selectComp.code" :release-code="`${selectComp.code}_Next`"></release-log>
    <comp-order ref="compOrder" :code="selectComp.code"></comp-order>
    <auth-agreement ref="authAgreement" :code="selectComp.code"></auth-agreement>
    <invoice-info ref="invoiceInfo" :code="selectComp.code"></invoice-info>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      backName: 'Filter',
      imgUrl: require('../../assets/image/plugin/FilterComplexInput.gif'),
      apiDesc: [
        `
        首先安装 vxe-table、node-sass 及相关依赖

        里面有 Demo 可供参考，购买授权后可以自行根据项目需求去定制修改，默认用于本地筛选

        安装：
        将 plugins/* 复制到项目任意目录中，引用 *.vue 即可

        参数：
        filters=[{data: {sType: 'include', sVal: '', isSensitive: false}}]"
        
        说明：
        sType: 筛选类型（incl包含、eq等于、gt大于、lt小于）
        sVal：搜索的值
        isSensitive：是否区分大小写
        
        参数：
        filter-render={ name: 'FilterComplexInput', cellType: 'string', props: {} }
        
        说明：
        cellType: 单元格的类型（string字符串、number数值）
        props：对应 vxe-input 配置项
        `
      ],
      demoCodes: [
        `
        <vxe-table
          border
          show-overflow
          height="500"
          :data="tableData">
          <vxe-table-column type="seq" width="60"></vxe-table-column>
          <vxe-table-column field="a" title="A1" :filters="[{ data: { isSensitive: false, sVal: '' } }]" :filter-render="{ name: 'FilterSimpleInput', cellType: 'number' }"></vxe-table-column>
          <vxe-table-column field="b" title="B1" :filters="[{ data: { isSensitive: false, sVal: '' } }]" :filter-render="{ name: 'FilterSimpleInput' }"></vxe-table-column>
          <vxe-table-column field="c" title="C1" :filters="[{ data: { sType: 'include', isSensitive: false, sVal: '' } }]" :filter-render="{ name: 'FilterComplexInput', cellType: 'number' }"></vxe-table-column>
          <vxe-table-column field="d" title="D1" :filters="[{ data: { sType: 'include', isSensitive: false, sVal: '' } }]" :filter-render="{ name: 'FilterComplexInput' }"></vxe-table-column>
        </vxe-table>
        `,
        `
        export default {
          data () {
            return {
              tableData: [
                { id: 10001, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10002, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10003, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10004, a: '', b: '', c: '', d: '', e: '', f: '', g: '' }
              ]
            }
          }
        }
        `
      ]
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList',
      'appCompList'
    ]),
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList && this.selectGroupComp) {
        return this.loginInfo.compList.find(comp => this.selectGroupComp.options.some(gItem => gItem.code === comp.appCode))
      }
      return null
    },
    selectGroupComp () {
      return this.appCompList.find(item => item.gCode === 'FilterComplexInput')
    },
    selectComp () {
      const gItem = this.selectGroupComp
      if (gItem) {
        return gItem.options.find(item => item.code === gItem.selected)
      }
      return null
    }
  }
}
</script>
