<template>
  <div v-if="selectComp" class="page-view">
    <div class="comp-tip">
      <div>
        <template v-if="authComp">
          <span class="comp-name">{{ selectComp.name }}</span>
          <span class="comp-support">
            <span>技术支持有效期：</span>
            <span v-if="loginInfo.currDate > authComp.supportExpiryDate" class="content red">已过期</span>
            <span v-else class="content green">{{ authComp.supportExpiryDate }}</span>
          </span>
          <template v-if="authComp.isInvoice">
            <vxe-button type="text" class="invoice-btn" status="primary" @click="$refs.invoiceInfo.open()" content="查看发票"></vxe-button>
          </template>
          <template v-else-if="selectComp.invoice">
            <span v-if="$utils.isWithin30Day(authComp.authDate)" class="comp-invoice">
              <vxe-button type="text" class="invoice-btn" status="primary" @click="$refs.invoiceInfo.submit()">申请发票<span class="red" style="font-size: 12px;">({{ $utils.toDateString($utils.getWhatDay(authComp.authDate, 7), 'yyyy-MM-dd') }}截止)</span></vxe-button>
            </span>
          </template>
        </template>
        <template v-else>
          <vxe-select class="comp-name" v-model="selectGroupComp.selected" :options="selectGroupComp.options" :option-props="{label: 'name', value: 'code'}" size="mini"></vxe-select>
          <span class="comp-type"> - {{ $utils.getCompType(selectComp.authCodeType) }}</span>
          <template v-if="selectComp.pricing !== selectComp.amount">
            <span class="comp-pricing">￥{{ selectComp.pricing }}</span>
            <span class="comp-amoun">￥{{ selectComp.amount }}</span>
          </template>
          <template v-else>
            <span class="comp-amoun">￥{{ selectComp.amount }}</span>
          </template>
          <span class="comp-expire">/ {{ $utils.getCompExpiration(selectComp.deadline) }}</span>
          <span class="comp-support" v-if="selectComp.supportDay >= 365">提供 1 年技术支持</span>
        </template>
      </div>
      <template v-if="loginInfo && authComp">
        <ul>
          <li>授权方式：<span class="green bold">{{ $utils.getCompType(authComp.authType) }}</span></li>
          <li>授权时间：<span class="green bold">{{ $utils.getCompExpiration(authComp.deadline) }}</span></li>
        </ul>
      </template>
      <template v-else>
        <ul>
          <li>{{ selectComp.authCodeType === 'src' ? '该授权包含完整源码' : '该授权不提供源码'}}</li>
        </ul>
      </template>
      <div class="comp-agreement">
        <a class="link" @click="$refs.authAgreement.open()">查看详细协议</a>
      </div>
      <div class="comp-oper">
        <vxe-button class="btn" status="danger" @click="$refs.compOrder.open()" v-if="!authComp">购买授权</vxe-button>
        <vxe-button class="btn" status="primary" @click="$refs.releaseLog.open()">{{ authComp ? '下载 v3.0 版本' : '查看 v3.0 版本' }}</vxe-button>
        <vxe-button class="btn" status="primary" @click="$refs.releaseNextLog.open()">{{ authComp ? '下载 v4.0 版本' : '查看 v4.0 版本' }}</vxe-button>
        <vxe-button class="btn" @click="$router.replace({name: backName})">返回</vxe-button>
      </div>
    </div>
    <div class="comp-preview">
      <div class="preview">
        <img :src="imgUrl">
      </div>
      <ul class="features">
        <li class="red">单元格选取功能不支持树形结构及展开行</li>
        <li v-for="(item, index) in selectComp.features" :key="index">{{ item }}</li>
      </ul>
    </div>
    <div class="comp-api">
      <a class="link" href="/vxe-table/#/table/api?q=pro" target="_black">查看更多 API</a>
    </div>
    <div class="comp-desc">
      <pre>
        <pre-code class="text">{{ apiDesc[0] }}</pre-code>
      </pre>
    </div>
    <pre>
      <pre-code class="html">{{ demoCodes[0] }}</pre-code>
      <pre-code class="javascript">{{ demoCodes[1] }}</pre-code>
    </pre>
    <release-log ref="releaseLog" :code="selectComp.code"></release-log>
    <release-log ref="releaseNextLog" :code="selectComp.code" :release-code="`${selectComp.code}_Next`"></release-log>
    <comp-order ref="compOrder" :code="selectComp.code"></comp-order>
    <auth-agreement ref="authAgreement" :code="selectComp.code"></auth-agreement>
    <invoice-info ref="invoiceInfo" :code="selectComp.code"></invoice-info>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      backName: 'Pro',
      imgUrl: require('../../assets/image/plugin/proClip.gif'),
      apiDesc: [
        `
        首先安装 vxe-table 及相关依赖

        安装：
        里面有 Demo 可供参考，将 plugins/* 复制到项目任意目录中，引用 *.es6.min.js 和 *.min.css 即可

        `
      ],
      demoCodes: [
        `
        <vxe-grid v-bind="gridOptions"></vxe-grid>
        `,
        `
        export default {
          data () {
            return {
              gridOptions: {
                border: true, // 显示边框
                resizable: true, // 列宽拖动调整
                autoResize: true, // 自适应宽高
                keepSource: true, // 支持编辑状态显示
                showOverflow: true, // 单元格溢出隐藏
                height: 600, // 表格高度

                // 可编辑配置
                editConfig: {
                  trigger: 'dblclick', // 双击单元格编辑
                  mode: 'cell', // 单元格编辑模式
                  showUpdateStatus: true, // 显示编辑状态
                  showInsertStatus: true // 显示新增状态
                },

                // 鼠标配置
                mouseConfig：{
                  area: true, // 是否开启鼠标单元格区域选取
                  extension: true // 是否开启鼠标点击区域内右下角扩展按钮（快捷键 Shift + 方向键扩大区域）
                },

                // 区域选取配置（只对 mouse-config.area 有效）
                areaConfig: {
                  selectCellByHeader: true, // 点击列头是否选取当前列的所有单元格
                  extendByCopy: false, // 点击右下角扩展按钮，将被选取区域的值复制到扩展区域中（同时按住 shift 键可取消值复制功能）
                  extendByCalc: false // 当选取大于两行或两列时，扩展区域时将自动识别数字规则进行计算（同时按住 ctrl 键可取消值自动识别数字功能）
                },

                // 复制粘贴配置项（只对 keyboard-config.isClip 有效）
                clipConfig: {
                  isCopy: true, // 是否启用复制功能（快捷键 Ctrl + C 键）
                  isCut: true, // 是否启用剪贴功能（快捷键 Ctrl + X 键）
                  isPaste: true, // 是否启用粘贴功能（快捷键 Ctrl + V 键）
                  isRowIncrement: false, // 是否启用行自增，当粘贴的行数超出表格时自动插入新行
                  isColumnIncrement: false // 是否启用列自增，当粘贴的列数超出表格时自动插入新列
                },

                // 查找与替换（只对 keyboard-config.isFNR 有效）
                fnrConfig: {
                  isFind: false, // 是否启用查找功能（快捷键 Ctrl + F 键）
                  isReplace: false // 是否启用替换功能（快捷键 Ctrl + H 键）
                },

                // 按键配置
                keyboardConfig：{
                  isClip: true, // 是否开启（Ctrl + X 键、Ctrl + C 键、Ctrl + V 键）复制粘贴功能
                  isMerge: true, // 是否开开启（Ctrl + M 键）合并和取消合并功能
                  isFNR: true, // 是否开启（Ctrl + F 键、Ctrl + H 键）查找和替换功能
                  isEdit: true, // 是否开启（Esc 键、F2 键、任意键）单元格编辑功能
                  isTab: true, // 是否开启（Tab 键、Shift + Tab 键）单元格左右移动功能
                  isArrow: true, // 是否开启（方向键）单元格上下左右移动功能
                  isEnter: true, // 是否开启（Enter 键、Shift + Enter 键）单元格上下移动功能
                  isDel: true, // 是否开启（Delete 键、Backspace 键）清空单元格的值
                  isChecked: true, // 是否开启（Spacebar 键）切换复选框和单选框状态
                  enterToTab: true // 是否将回车键行为改成 Tab 键行为
                },

                // 右键菜单配置
                menuConfig: {
                  body: {
                    options: [
                      [
                        { code: 'COPY_CELL', name: '复制 (Ctrl+C)' }, // 将选取的区域复制到剪贴板（可用于 Excel 和 WPS）
                        { code: 'CUT_CELL', name: '剪贴 (Ctrl+X)' }, // 将选取的区域剪贴到剪贴板（可用于 Excel 和 WPS）
                        { code: 'PASTE_CELL', name: '粘贴 (Ctrl+V)' } // 从表格中读取已复制的区域，粘贴到当前区域中（可用于 Excel 和 WPS）
                      ],
                      [
                        { code: 'OPEN_FIND', name: '查找 (Ctrl+F)' }, // 打开查找窗口
                        { code: 'OPEN_REPLACE', name: '替换 (Ctrl+H)' }, // 打开替换窗口
                        { code: 'MERGE_OR_CLEAR', name: '合并/取消 (Ctrl+M)' } // 将选取的区域进行合并与取消
                      ],
                      [
                        { code: 'INSERT_AT_ROW', name: '新增' }, // 但当前行中插入新行
                        { code: 'EDIT_CELL', name: '编辑' }, // 将单元格设置为可编辑状态
                        { code: 'DELETE_ROW', name: '删除' } // 删除当前行
                      ],
                      [
                        {
                          name: '排序',
                          children: [
                            { code: 'CLEAR_SORT', name: '清除排序' }, // 清除所有排序
                            { code: 'SORT_ASC', name: '升序' }, // 将当前列进行升序排序
                            { code: 'SORT_DESC', name: '倒序' } // 将当前列进行倒序排序
                          ]
                        }
                      ],
                      [
                        {
                          name: '创建图表',
                          prefixIcon: 'fa fa-area-chart',
                          children: [
                             // 根据所选区域的数据，生成柱状图
                            { code: 'CHART_BAR_X_AXIS', name: '横向柱状图 - 自由选择' },
                            { code: 'CHART_BAR_X_AXIS', name: '横向柱状图 - 固定类别', params: { category: 'a' } },
                            { code: 'CHART_BAR_Y_AXIS', name: '纵向柱状图 - 自由选择' },
                            { code: 'CHART_BAR_Y_AXIS', name: '纵向柱状图 - 固定类别', params: { category: 'a' } },
                             // 根据所选区域的数据，生成折线图
                            { code: 'CHART_LINE', name: '折线图 - 自由选择' },
                            { code: 'CHART_LINE', name: '折线图 - 固定类别', params: { category: 'a' } },
                             // 根据所选区域的数据，生成饼图
                            { code: 'CHART_PIE', name: '饼图 - 自由选择' },
                            { code: 'CHART_PIE', name: '饼图 - 固定类别', params: { category: 'a' } }
                          ]
                        }
                      ]
                    ]
                },

                // 列配置
                columns: [
                  { type: 'seq', width: 60 },
                  {
                    title: '分组1',
                    children: [
                      { field: 'a', title: 'A', align: 'right', width: 120, editRender: { name: '$input' } },
                      { field: 'b', title: 'B', width: 180, editRender: { name: '$input' } },
                      { field: 'c', title: 'C', width: 200, editRender: { name: '$input' } },
                      { field: 'd', title: 'D', width: 160, editRender: { name: '$input' } }
                    ]
                  },
                  {
                    title: '分组1',
                    children: [
                      { field: 'e', title: 'E', width: 100, editRender: { name: '$input' } },
                      { field: 'f', title: 'F', width: 140, editRender: { name: '$input' } },
                      { field: 'g', title: 'G', width: 160, editRender: { name: '$input' } },
                      { field: 'h', title: 'H', width: 120, editRender: { name: '$input' } }
                    ]
                  },
                  { field: 'i', title: 'I', width: 200, editRender: { name: '$input' } },
                  { field: 'j', title: 'J', width: 100, editRender: { name: '$input' } },
                  { field: 'k', title: 'K', width: 160, editRender: { name: '$input' } },
                  { field: 'l', title: 'L', width: 100, editRender: { name: '$input' } },
                  { field: 'm', title: 'M', width: 120, editRender: { name: '$input' } },
                  { field: 'n', title: 'N', width: 180, editRender: { name: '$input' } }
                ],

                // 数据源
                data: [
                  { id: 10001, a: '', b: '', c: '', d: '', e: '', f: '', g: '', h: '', i: '', j: '', k: '', l: '', m: '', n: '' },
                  { id: 10002, a: '', b: '', c: '', d: '', e: '', f: '', g: '', h: '', i: '', j: '', k: '', l: '', m: '', n: '' },
                  { id: 10003, a: '', b: '', c: '', d: '', e: '', f: '', g: '', h: '', i: '', j: '', k: '', l: '', m: '', n: '' },
                  { id: 10004, a: '', b: '', c: '', d: '', e: '', f: '', g: '', h: '', i: '', j: '', k: '', l: '', m: '', n: '' }
                ]
              }
            }
          }
        }
        `
      ]
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList',
      'appCompList'
    ]),
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList && this.selectGroupComp) {
        return this.loginInfo.compList.find(comp => this.selectGroupComp.options.some(gItem => gItem.code === comp.appCode))
      }
      return null
    },
    selectGroupComp () {
      return this.appCompList.find(item => item.gCode === 'PRO')
    },
    selectComp () {
      const gItem = this.selectGroupComp
      if (gItem) {
        return gItem.options.find(item => item.code === gItem.selected)
      }
      return null
    }
  }
}
</script>
