<template>
  <div class="preview-title" v-if="selectGroupComp && selectComp">
    <template v-if="authComp">
      <span class="comp-auth">已获得授权</span>
      <vxe-button class="btn" status="primary" size="mini" @click="$router.replace({name: selectGroupComp.gCode})" style="margin-left: 15px;">查看详情</vxe-button>
    </template>
    <template v-else>
      <vxe-select class="comp-name" v-model="selectGroupComp.selected" :options="selectGroupComp.options" :option-props="{label: 'name', value: 'code'}" size="mini" transfer></vxe-select>
      <span class="comp-type"> - {{ $utils.getCompType(selectComp.authCodeType) }}</span>
      <template v-if="selectComp.pricing !== selectComp.amount">
        <span class="comp-pricing">￥{{ selectComp.pricing }}</span>
        <span class="comp-amoun">￥{{ selectComp.amount }}</span>
      </template>
      <template v-else>
        <span class="comp-amoun">￥{{ selectComp.amount }}</span>
      </template>
      <span class="comp-expire">/ {{ $utils.getCompExpiration(selectComp.deadline) }}</span>
      <vxe-button class="btn" status="danger" size="mini" @click="$router.replace({name: selectGroupComp.gCode})" style="margin-left: 15px;">获取授权</vxe-button>
    </template>
    <template v-if="!authComp || !$utils.isWithin30Day(authComp.authDate)">
      <vxe-button class="btn" status="primary" type="text" @click="$refs.releaseLog.open()">{{ authComp ? 'v3 版本' : 'v3 版本' }}</vxe-button>
      <vxe-button class="btn" status="primary" type="text" @click="$refs.releaseNextLog.open()">{{ authComp ? 'v4 版本' : 'v4 版本' }}</vxe-button>
    </template>
    <release-log ref="releaseLog" :code="selectComp.code"></release-log>
    <release-log ref="releaseNextLog" :code="selectComp.code" :release-code="`${selectComp.code}_Next`"></release-log>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  props: {
    selectGroupComp: Object,
    selectComp: Object
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList',
      'appCompList'
    ]),
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList && this.selectGroupComp) {
        return this.loginInfo.compList.find(comp => this.selectGroupComp.options.some(item => item.code === comp.appCode))
      }
      return null
    }
  }
}
</script>
