<template>
  <div v-if="supportQQComp" class="support-page">
    <h2 class="support-title">
      <span>{{ supportQQComp.name }}</span>
      <span class="price-unit">￥{{ supportQQComp.amount }}</span>
      <div style="font-size: 12px;font-weight: 400;line-height: 30px">（{{ supportQQComp.features.join('; ') }}）</div>
    </h2>
    <div class="support-group">
      <div class="qq-step">
        <ul class="step-wrapper">
          <template v-if="disabledSupport">
            <li title="抱歉！当前不在支持的时间之内！请在工作日 9:30 ~ 17:30 时间内申请！">抱歉，当前不在支持的时间之内</li>
          </template>
          <template v-else>
            <li style="font-size: 15px;font-weight: 700;user-select: none;">
              <vxe-checkbox v-model="useIssue">我会使用 “<a class="link" href="/vxe-table/issues" target="_blank">在线 Demo 演示问题</a>”</vxe-checkbox>
            </li>
            <li style="font-size: 15px;font-weight: 700;user-select: none;">
              <vxe-checkbox v-model="supportAuthor">同意加入支持群赞助作者</vxe-checkbox>
            </li>
          </template>
        </ul>
        <div class="pay-btns">
          <template v-if="checkAllOption">
            <vxe-button class="btn" status="danger" size="mini" @click="$refs.compOrder.open()">支持作者</vxe-button>
          </template>
        </div>
      </div>
    </div>
    <comp-order ref="compOrder" :code="code"></comp-order>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      code: 'SupportQQ',
      useIssue: false,
      supportAuthor: false
    }
  },
  computed: {
    ...mapState([
      'appList',
      'serveTime'
    ]),
    checkAllOption () {
      return this.useIssue && this.supportAuthor
    },
    supportQQComp () {
      return this.appList.find(item => item.code === 'SupportQQ')
    },
    disabledSupport () {
      const currDate = new Date(this.serveTime)
      const day = currDate.getDay()
      const hours = currDate.getHours()
      if (day === 0 || !((hours >= 9 && hours <= 11) || (hours >= 14 && hours <= 17) || (hours >= 19 && hours <= 21))) {
        return true
      }
      return false
    }
  }
}
</script>

<style lang="scss" scoped>
.support-title {
  text-align: center;
}
.price-unit {
  font-size: 18px;
  color: #ff6700;
  padding: 0 5px;
  font-weight: 700;
}
.support-group {
  margin: 15px auto;
  width: 300px;
  .step-wrapper {
    & > li {
      list-style-type: circle;
      line-height: 32px;
    }
  }
  .pay-btns {
    text-align: center;
  }
  .pay-link {
    color: #22AB38;
    cursor: pointer;
  }
}
.is-disabled {
  cursor: no-drop;
}
.support-pay {
  margin-top: 50px;
  text-align: center;
}
</style>
