import Vue from 'vue'
import VueRouter from 'vue-router'
import Pay from '../views/Pay.vue'
import Support from '../views/Support.vue'
import Pro from '../views/Pro.vue'
import ProAuth from '../views/ProAuth.vue'
import Filter from '../views/Filter.vue'
import Edit from '../views/Edit.vue'

import ProExtension from '../views/pro/Extension.vue'

import FilterComplexInput from '../views/filter/ComplexInput.vue'
import FilterCombination from '../views/filter/Combination.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: {
      name: 'Pro'
    }
  },
  {
    path: '/pay',
    name: 'Pay',
    component: Pay
  },
  {
    path: '/support',
    name: 'Support',
    component: Support
  },
  {
    path: '/pro',
    name: 'Pro',
    component: Pro
  },
  {
    path: '/proAuth',
    name: 'ProAuth',
    component: ProAuth
  },
  {
    path: '/filter',
    name: 'Filter',
    component: Filter
  },
  {
    path: '/pro/Extension',
    name: 'ProExtension',
    component: ProExtension
  },
  {
    path: '/filter/ComplexInput',
    name: 'FilterComplexInput',
    component: FilterComplexInput
  },
  {
    path: '/filter/Combination',
    name: 'FilterCombination',
    component: FilterCombination
  },
  {
    path: '/edit',
    name: 'Edit',
    component: Edit
  }
]

if (process.env.NODE_ENV === 'development') {
  routes.push(
    {
      path: '/filter/TestA',
      name: 'FilterTestA',
      component: () => import('../views/filter/TestA.vue')
    }
  )
}

const router = new VueRouter({
  routes
})

export default router
