import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import 'font-awesome/css/font-awesome.css'
import './plugins'
import './assets/style/layput.scss'

import EmailService from './components/EmailService.vue'
import QQService from './components/QQService.vue'
import ReleaseLog from './components/ReleaseLog.vue'
import CompOrder from './components/CompOrder.vue'
import AuthAgreement from './components/AuthAgreement.vue'
import InvoiceInfo from './components/InvoiceInfo.vue'

Vue.component(EmailService.name, EmailService)
Vue.component(QQService.name, QQService)
Vue.component(ReleaseLog.name, ReleaseLog)
Vue.component(CompOrder.name, CompOrder)
Vue.component(AuthAgreement.name, AuthAgreement)
Vue.component(InvoiceInfo.name, InvoiceInfo)

Vue.config.productionTip = false

if (process.env.NODE_ENV !== 'development') {
  document.onkeydown = evnt => {
    if (evnt.key.toLowerCase() === 'f12') {
      if (evnt.preventDefault) {
        evnt.preventDefault()
      } else {
        evnt.returnValue = false
      }
    }
  }
  document.oncontextmenu = evnt => {
    if (evnt.preventDefault) {
      evnt.preventDefault()
    } else {
      evnt.returnValue = false
    }
  }
}

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
